
<template>
    <div>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">

                        <div class="modal-header ui-draggable-handle  py-0">
                            <button type="button" class="close" v-on:click="CloseCustomFieldsPopup()">
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                            <h4 name="header" class="modal-title py-3">
                                {{ $t('AddCustomFields') }}
                            </h4>
                        </div>

                        <div class="container-fluid padding-t_10">

                            <div class="left-menu-tab">
                                <div class="row">
                                    <div class="col-md-12">
                                        <div class="theme-primary partition">
                                            <span class="p-name text-white">User Defined Fields:</span>
                                        </div>
                                    </div>
                                </div>
                                <div class="accordion md-accordion left-filter" id="accordionEx" role="tablist"
                                    aria-multiselectable="true">
                                    <div class="card-panel">
                                        <div class="card">
                                            <div id="collapseOne13" class="collapse show" role="tabpanel"
                                                aria-labelledby="headingOne12" data-parent="#accordionEx">
                                                <div class="card-body p-2">
                                                    <draggable v-model="systemFieldList" class="draggable-list"
                                                        :list="systemFieldList" @start="drag = true"
                                                        @end="AllowOneElementdrop()" item-key="Data_type_id"
                                                        group="fieldList" tag="ul">
                                                        <!-- class="list-group ulparted pb-0" -->

                                                        <template v-for="element in systemFieldList">
                                                            <div class="row">
                                                                <div class="col-sm-12 col-md-12">
                                                                    <div v-bind:class="{
                                                                        'user-defined-fields': true, 'fa fa-navicon': element.display_name == 'MultiLine',
                                                                        'fa fa fa-ellipsis-h': element.display_name == 'SingleLine', 'fa fa-list': element.display_name == 'Selectlist',
                                                                        'fa fa-sort-numeric-asc': element.display_name == 'Integer', 'fa  fa-list-ol': element.display_name == 'LongInteger',
                                                                        'fa fa fa-circle': element.display_name == 'Decimal', 'fa fa-calendar-o': element.display_name == 'Date',
                                                                        'fa fa-check-square-o': element.display_name == 'Checkbox', 'fa fa-dot-circle-o': element.display_name == 'Radio',
                                                                        'fa fa-at': element.display_name == 'Url', 'fa fa-phone': element.display_name == 'Phone', 'fa fa-envelope': element.display_name == 'Email'
                                                                    }">
                                                                        <label class="pl-2 font-16 fontpoppins">{{
                                                                            $t(element.display_name) }}</label>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </template>
                                                    </draggable>
                                                </div>
                                            </div>
                                        </div>

                                    </div>


                                </div>

                            </div>

                            <div class="right-content">

                                <div class="col-lg-12 mb-0 float-left p-0">
                                    <div class="heading-border">
                                        <h5 class="h5">
                                            <span class="spngropuname">
                                                {{ $t('Detail') }}
                                            </span>
                                        </h5>
                                    </div>
                                </div><br /><br />
                                <div class="dashed_bod_drag text-center mt-2 py-1">
                                    <h3 v-if="disabled == false">Drop here </h3>
                                    <draggable v-model="dropfields" style="min-height:50px;" class="draggable-list"
                                        :list="dropfieldslist" id="dragList" :disabled="disabled" @start="drag = true"
                                        @end="drag = true" group="fieldList" tag="ul" item-key="Data_type_id">
                                        <!-- group="my-groupForFields" -->
                                        <template v-for="element in dropfieldslist">
                                            <div class="list-item parent-element">
                                                <div class="row">
                                                    <div class="col-lg-12 d-flex">
                                                        <input type="text" class="w-60 py-1 px-2 bg-white"
                                                            :placeholder="FieldPropertyData.length > 0 ? FieldPropertyData[0].Name : element.Data_type_name"
                                                            disabled />
                                                        <div class="field-action mt-2 parentdiv" id="MainDivForLayout">
                                                            <div class="dropdown">
                                                                <a class="action-icon" id="dropdownMenuButton" href="#"
                                                                    @click="openpopupfordraggedfield($event)">
                                                                    <em class="fa fa-ellipsis-h text-secondary"></em></a>
                                                                <div>
                                                                    <div class="verticle-action ">
                                                                        <span class="action-list-box small filter-list-box">
                                                                            <span id="action-list"
                                                                                class="list-actions text-left border-0 p-0">
                                                                                <span
                                                                                    class="dropdown-menu dropdown-menu-dark filter-list-box d-block"
                                                                                    style="left:15px;">
                                                                                    <em aria-hidden="true"
                                                                                        class="fa fa-times"
                                                                                        style="position: absolute;right: -6px;top: -12px;z-index: 1;"
                                                                                        @click="closepopupfordraggedfield"></em>
                                                                                    <div
                                                                                        class="modal-header ui-draggable-handle  py-0">
                                                                                    </div>
                                                                                    <a class="dropdown-item px-2 py-2"
                                                                                        href="javascript:;"
                                                                                        @click="OpenPropertyPopup(element.Data_type_id, element.Data_type_name)">
                                                                                        <em class="fa fa-eye text-primary float-left my-0 ml-1"
                                                                                            style="font-size:16px;"></em>
                                                                                        <span class="ml-1 text-secondary"
                                                                                            style="font-size:14px;">{{ $t('ViewProperties') }}</span>
                                                                                    </a>
                                                                                    <div class="dropdown-divider m-0"></div>
                                                                                    <a class="dropdown-item px-2 py-2"
                                                                                        href="javascript:;"
                                                                                        @click="RemoveField(element.Data_type_id)">
                                                                                        <em class="fa fa-trash text-danger float-left my-0 ml-1"
                                                                                            style="font-size:16px;"></em>
                                                                                        <span class="ml-1 text-dark"
                                                                                            style="font-size:14px;">{{$t('RemoveField')}}</span>
                                                                                    </a>
                                                                                </span>
                                                                            </span>
                                                                        </span>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </template>
                                    </draggable>
                                </div>
                                <span class="invalid-feedback d-block">{{ errorMessage }} </span>
                            </div>
                            <div class="col-lg-12 p-0 text-right my-3 float-left">

                                <a href="javascript:;" v-bind:class="{ 'btn btn-success mr-2': true }"
                                    v-on:click="SubmitData()" title="Save"><em class="fa fa-save pr-2"></em>Submit</a>
                                <button class="btn btn-danger formbtn widthhalf" title="Cancel"
                                    @click="CloseCustomFieldsPopup"><em class="fa fa-close pr-2"></em>Cancel</button>
                            </div>


                        </div>
                    </div>
                </div>

            </div>
        </div>
        <CustomFieldPropertyPopup :oldFieldPropertyData=oldFieldPropertyData v-if="showFieldPropPopup"
            :divManageLayoutProp="divManageLayoutProp" :iSLayoutControl="iSLayoutControl" :DataTypeId="DataTypeId"
            :FieldName="FieldName" :LanguageArray="LanguageArray"></CustomFieldPropertyPopup>
    </div>
</template>

<script>
import draggable from 'vuedraggable';
import DataService from '../../../services/DataService.js';
import CustomFieldPropertyPopup from '../Modal/CustomFieldPropertyPopup.vue'

export default {
    props: {

        LanguageArrayData:
        {
            type: Array,
            required: true
        },
        Sub_ModuleId:
        {
            type: String,
            required: true
        },
        ModuleId:{
            type: String,
            required: true
        }
    },
    components: {
        draggable,
        DataService,
        CustomFieldPropertyPopup
    },
    data() {
        return {
            drag: false,
            dropfieldslist: [],
            dropfields: [],
            systemFieldsModel: '',
            OldIdForProp: '',
            systemFieldList: [],
            showFieldPropPopup: false,
            FieldName: '',
            DataTypeId: '',
            iSLayoutControl: false,
            divManageLayoutProp: false,
            LanguageArray: [],
            FieldPropertyData: [],
            oldFieldPropertyData: [],
            finalJsonData: [],
            // closepopupMenuhide:false,
            controlOnStart: true,
            bindEditedDataProperty: [],
            disabled: false,
            errorMessage: '',
        }
    },
    created: function () {
        var vm = this;
        vm.GetCustomFieldDataTypes()
    },
    methods:
    {
        CloseCustomFieldsPopup() {
            debugger
            this.$parent.hidePopup();
        },
        openpopupfordraggedfield(event) {
            debugger
            $(".filter-list-box").width('0px').css("overflow", "hidden");
            $(event.target).closest(".parentdiv").find(".filter-list-box").css("overflow", "visible");
        },
        closepopupfordraggedfield: function () {
            $(".filter-list-box").width('0px').css("overflow", "hidden");
        },
        endDrag($vent, type) {

        },
        AllowOneElementdrop() {
            var vm = this;
            if (vm.dropfields == null) {
                vm.disabled = false;
            }
            if (vm.dropfields != null) {
                vm.disabled = true;
            }
            else {
                vm.disabled = false;
            }
        },
        clone({ name }) {
            return { name };
        },
        pullFunction() {
            return this.controlOnStart ? "clone" : true;
        },
        closeAddCustomFieldPopup() {
            var vm = this;
            vm.$parent.closeAddCustomField();
        },
        RemoveField: function (id) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
                var checkindex = vm.dropfieldslist.findIndex(x => x.Data_type_id === id);
                if (checkindex > -1) {
                    vm.systemFieldList.push(vm.dropfieldslist[checkindex]);
                    vm.dropfieldslist.splice(checkindex, 1);
                    vm.FieldPropertyData = [];
                    if (vm.dropfields == '') {
                        vm.disabled = false;
                    }
                }

            });
        },
        SubmitData() {
            var vm = this;
            vm.closepopupfordraggedfield();
            if (vm.FieldPropertyData.length != 0) {
                vm.FieldPropertyData.forEach((item) => {
                    let mainObj = {
                        CUSTOM_FIELD_ID: 0,
                        MODULE_ID: vm.ModuleId,
                        SUB_MODULE_ID: vm.Sub_ModuleId,
                        NAME: item.Name,
                        DISPLAY_NAME: item.displayName,
                        DESCRIPTION: item.Description,
                        PICKLIST_OPTION: item.PickListOption,
                        TYPE_ID: item.id,
                        LENGTH: item.Length,
                        IS_REQUIRED: item.IsRequired == 'undefined' ? 0 : 1,
                        STATUSID: 1001,
                        DECIMAL_PLACES: item.DecimalPlaces,
                        LOC_VALUE: item.LanguageName,
                        FORM_FIELD_VISIBILITY: 'YES',
                        LIST_FIELD_VISIBILITY: 'YES'
                    }
                    vm.finalJsonData.push(mainObj);
                });
                var params = `jsonString=${JSON.stringify(vm.finalJsonData)}`
                DataService.SaveUpdateCustomField(params).then(function (response) {
                    var checkstatus = JSON.parse(response.data)
                    if (checkstatus.STATUS_CODE == 1) {
                        vm.ShowAlert(vm.$t('CustomFieldSuccess', [vm.$t('Field')]), "success", true, vm.$t("Alert"));
                        vm.$emit('GetCustomNewField', vm.finalJsonData);

                        vm.CloseCustomFieldsPopup();
                    }
                    else if (checkstatus.STATUS_CODE == 3) {
                        vm.ShowAlert(vm.$t('CustomFieldExists'), "danger", true, vm.$t("Alert"));
                    }
                    else {
                        vm.ShowAlert(vm.$t('CustomFieldUpdate'), "success", true, vm.$t("Alert"));
                        vm.$emit('GetCustomNewField', vm.finalJsonData);
                        vm.CloseCustomFieldsPopup();
                    }
                })
            }
            else {
                vm.errorMessage = 'Property is not set for this custom field.';
            }
        },
        closePropPopup(myObj) {
            var vm = this;
            if (myObj != null) {
                vm.FieldPropertyData = myObj;
            }
            vm.showFieldPropPopup = false;
        },
        // Open popup to edit custom field property.
        OpenPropertyPopup(id, name) {
            var vm = this;
            if (vm.FieldPropertyData.length > 0) {
                var chkIndexForEditProperty = vm.dropfieldslist.findIndex(x => x.Data_type_id === id);
                if (chkIndexForEditProperty > -1) {
                    vm.bindEditedDataProperty.push(vm.dropfieldslist[chkIndexForEditProperty]);
                }
            }
            vm.DataTypeId = id;
            vm.FieldName = name;
            vm.iSLayoutControl = true;
            vm.divManageLayoutProp = false;
            vm.LanguageArray = vm.LanguageArrayData;
            vm.oldFieldPropertyData = vm.FieldPropertyData;
            vm.showFieldPropPopup = true;

        },
        GetCustomFieldDataTypes() {
            var vm = this;
            DataService.GetCustomFieldDataType().then(function (response) {
                if (JSON.parse(response.data) != null) {
                    vm.systemFieldList = JSON.parse(response.data).data;
                }
            })
        }
    }
}
</script>
<style>.makepopleft#mainDivProp .dropdown-menu {
    inset: unset !important;
    transform: unset !important;
    right: 0px !important;
}</style>