<template lang="en">
<div class="my-popups">
    <div class="modal d-block ">
        <div class="modal-dialog modal-dialog-centered ui-draggable">
            <div class="modal-content">

                <div class="modal-header ui-draggable-handle  py-0">
                    <button type="button" class="close" @click="closePopup">
                        <em aria-hidden="true" class="fa fa-times"></em>
                    </button>
                    <h4 name="header" class="modal-title py-3">
                        {{ $t('CustomFieldDependency') }} {{SelectedCustomFieldName}}
                    </h4>

                </div>
                <div class="listing py-3 px-3">
                    <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                        <template v-slot:tgslot-ProductTypeCode="{ data }" v-if="DependencyData != null">
                            <div class="col-lg-12 p-0 workdiary_sanp_row border p-3">
                                <div class="timeline-panel row">
                                    <!-- <ul v-for="item in GetMultiSelectValue" :key="index">

                                            <li>{{item.label}}</li>
                                        </ul> -->
                                    <div class="col-md-6">
                                        <ul class="list-group">
                                            <li class="list-group-item" v-for="(item, index) in GetMultiSelectValue" :key="index" @click="showWhenSelectItem(item)" :class="{ sel: selectedItem && selectedItem.name === item.name }">
                                                {{ item.name }}
                                            </li>
                                        </ul>
                                    </div>
                                    <div class="col-md-6 border-left pl-5">
                                        <div class="scroldv">
                                            <ul class="list-group " id="rightList">
                                                <li class="list-group-item" v-for="itm in DependencyData">

                                                    <label class="form-check form-check-inline">
                                                        <div class="custom-control custom-checkbox custom-control-inline">

                                                            <input @change="checkedSelected(itm)" :id="itm.value" type="checkbox" class="chkItems custom-control-input" value="53053">
                                                            <label :for="itm.value" class="custom-control-label">{{itm.name}}</label>
                                                            <input type="hidden" :value="itm.value">
                                                        </div>
                                                    </label>

                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </template>
                    </dynamic-form>
                </div>

            </div>
        </div>
    </div>
</div>
</template>

<script>
import {
    DynamicForm
} from 'tg-controls_cli/dist/myLib.common';
import DataService from "../../services/DataService.js";
export default {
    components: {
        DynamicForm
    },
    props: {
        CustomFieldId: {
            type: Number,
            required: true
        },
        DependencyData: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            dependentId: null,
            selectedItem: null,
            dependentOnDdlId: '',
            type: 'dependonddl',
            CustomData: [],
            dependentListArray: [],
            CustomArray: [],
            SelectedCustomFieldName: null,
            GetMultiSelectValue: [],
            GetEditDependencyData: [],
            showWhenListSelectedItem: '',
            TagsSelectedArray: [],
            FormSchema: [{
                    layoutType: "Single",
                    Data: [{
                            astype: "SelectField",
                            label: this.$t('DependOn'),
                            name: "DependOn",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.GetCustomFieldDDL
                            },
                            validationRules: "required",
                        }

                    ],
                },
                {
                    layoutType: "Single",
                    Data: [{
                            astype: "MultiSelectField",
                            label: this.$t('ShowWhen'),
                            name: "ShowWhen",
                            value: '',
                            mode: "tag",
                            placeholder: "",
                            config: {
                                options: [],
                                onChange: this.GetMultiValues,
                                onRemove: this.removeRecord,
                            },
                            validationRules: "required",
                        },

                    ]
                },
                {
                    layoutType: "Single",
                    Data: [{
                        astype: "SlotField",
                        label: '',
                        name: "ProductTypeCode",
                        value: "",
                        placeholder: "",
                        config: {
                            options: [],
                        },
                    }]
                },
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closePopup
                }
            ]
        }

    },
    created: function () {
        var vm = this;
        //debugger
        vm.SelectedCustomFieldName = vm.$parent.customFieldName;
        vm.GetCustomFieldDependent();
        vm.editDependency();
    },
    methods: {
        GetCustomFieldDependent() {
            var vm = this;
            //debugger
            var DepartmentData = [];
            var params = `customFieldId=${vm.CustomFieldId}`
            DataService.GetCustomFieldDependentListing(params).then((response) => {
                DepartmentData = JSON.parse(response.data);
                if (DepartmentData.data != null) {
                    DepartmentData.data.forEach(item => {
                        (vm.FormSchema)[0].Data[0].config.options.push({
                            value: item.custom_field_id,
                            name: item.name
                        })
                    })
                }

            })

        },
        async GetCustomFieldDDL(e, field, dependentId) {
            var vm = this;
            var CustomData = [];
            if (dependentId) {
                vm.dependentOnDdlId = dependentId;
            } else {
                vm.dependentOnDdlId = e.target.value;
            }
            (vm.FormSchema)[1].Data[0].config.options = [];
            var params = `customFieldId=${vm.dependentOnDdlId}&Type=${vm.type}`;

            await DataService.GetCustomFieldShowWhenDDL(params).then((response) => {
                CustomData = JSON.parse(response.data);
                if (CustomData != null) {
                    CustomData.forEach(item => {
                        (vm.FormSchema)[1].Data[0].config.options.push({
                            value: item.value,
                            name: item.name
                        })
                    })

                }              
                if(e!=""){
                    if(e.target.value==vm.dependentId){
                    vm.editDependency();                    
                    }
                }              

                vm.TagsSelectedArray = [];
                vm.GetMultiSelectValue = [];
                (vm.FormSchema)[1].Data[0].value = [];
                $('#rightList input:checkbox').each(function (index, item) {
                    $(item).prop('checked', false);
                   });
            })
        },
        closePopup() {
            var vm = this;
            vm.$parent.isShowDependencyFieldPopup = false;
        },
        showWhenSelectItem(itm) {
            var vm = this;
            //debugger
            vm.selectedItem = itm;
            $('#rightList :checked').each(function (index, item) {
                $(item).prop('checked', false);
            });

            if (vm.dependentListArray.length > 0) {
                for (var i = 0; i < vm.dependentListArray.length; i++) {

                    if (itm.value == vm.dependentListArray[i].value) {

                        vm.dependentListArray[i].dependent.forEach(itmVal => {

                            $('#rightList input:checkbox').each(function (index, item) {
                                if ($(item).siblings('input[type="hidden"]').val() == itmVal) {
                                    $(item).prop('checked', true);
                                }
                            });

                        })
                    }
                }
            }

            vm.showWhenListSelectedItem = itm.value;
            if (vm.dependentListArray.filter(x => x.value == itm.value).length == 0) {
                var dependentValueObj = {
                    "value": itm.value,
                    "dependent": []
                }
                vm.dependentListArray.push(dependentValueObj);
            }
            if (JSON.parse(vm.GetEditDependencyData.value_mapping).length > 0) {
                for (var i = 0; i < vm.dependentListArray.length; i++) {

                    if (itm.value == vm.dependentListArray[i].value) {

                        JSON.parse(vm.GetEditDependencyData.value_mapping).forEach(itmVal => {

                            if (itmVal.value == itm.value) {
                                itmVal.dependent.forEach(itemValue => {
                                    $('#rightList input:checkbox').each(function (index, item) {
                                        if ($(item).siblings('input[type="hidden"]').val() == itemValue) {
                                            $(item).prop('checked', true);
                                        }
                                    });
                                });
                            }

                        })
                    }
                }
            }

            if (vm.dependentListArray.length > 0) {
                for (var i = 0; i < vm.dependentListArray.length; i++) {
                    if (vm.dependentListArray[i].value == itm.value) {
                        vm.dependentListArray[i].dependent = [];
                        $('#rightList :checked').each(function (index, item) {
                            vm.dependentListArray[i].dependent.push($(item).siblings('input[type="hidden"]').val());
                        });
                    }
                }
            }
        },
        checkedSelected(value) {
            var vm = this;
            //debugger
            if (vm.dependentListArray.length > 0) {
                for (var i = 0; i < vm.dependentListArray.length; i++) {
                    if (vm.dependentListArray[i].value == vm.showWhenListSelectedItem) {
                        vm.dependentListArray[i].dependent = [];
                        $('#rightList :checked').each(function (index, item) {
                            vm.dependentListArray[i].dependent.push($(item).siblings('input[type="hidden"]').val());
                        });
                    }
                }
            }
        },
        removeRecord(event, field, meta) {
            //debugger
            var vm = this;
            let getindex = vm.GetMultiSelectValue.findIndex(x => x.value === event.value);
            if (getindex != -1) {
                vm.GetMultiSelectValue.splice(getindex, 1);
            }
        },
        GetMultiValues() {
            var vm = this;
            var obj = (vm.FormSchema)[1].Data[0].config.options.filter(x => x.name == event.target.textContent)[0];
            obj.isSel = false;
            vm.GetMultiSelectValue.push(obj);
        },
        onSubmit(formfields, e) {
            var vm = this;
            var dependentOn = formfields.DependOn;
            var showWhenArray = [];
            var showWhen = JSON.stringify(Array.prototype.map.call(formfields.ShowWhen, function (item) {
                showWhenArray.push(item.value);
            }))

            var obj = {
                "depend_on": formfields.DependOn,
                "value": showWhenArray
            };
            var url = `customFieldDependencyId=&customFieldId=${vm.$parent.CustomFieldId}&dependentOn=${dependentOn}&showWhen=${JSON.stringify(obj)}&valueMapping=${JSON.stringify(vm.dependentListArray)}`
            DataService.ManageCustomFieldDependency(url).then(response => {
                if (response.data == '1') {
                    vm.ShowAlert(vm.$t('DependentSuccess'), "Success", true, vm.$t("Alert"));
                    vm.closePopup();
                } else if (response.data == '2') {
                    vm.ShowAlert(vm.$t('UpdatedSuccessfully'), "Success", true, vm.$t("Alert"));
                    vm.closePopup();
                }
            })
        },
        async editDependency() {
            var vm = this;
            vm.GetEditDependencyData = [];
            var dependentData = [];
            vm.TagsSelectedArray = [];
            vm.GetMultiSelectValue = [];
            (vm.FormSchema)[1].Data[0].value = [];
            var url = `customFieldId=${vm.$parent.CustomFieldId}&dependentOnId=`; //${vm.dependentOnDdlId}`
            await DataService.GetCustomFieldId(url).then(response => {
                dependentData = JSON.parse(response.data)
                if (dependentData.data.length > 0) {
                    vm.GetEditDependencyData = dependentData.data[0];

                }
            })

            vm.dependentId = dependentData.data[0].dependent_on;
            if (vm.dependentId != null || vm.dependentId != undefined || vm.dependentId != "") {
                //
                (vm.FormSchema)[0].Data[0].value = vm.dependentId;
                await vm.GetCustomFieldDDL('', '', vm.dependentId);
                const showWhenValues = JSON.parse(vm.GetEditDependencyData.show_when).value;
                const selectedValues = (vm.FormSchema)[1].Data[0].config.options.filter(item => {
                    return showWhenValues.includes(item.value);
                });

                vm.GetMultiSelectValue = selectedValues;
                vm.TagsSelectedArray = selectedValues.map(item => ({
                    label: item.name,
                    value: item.value
                }));
                (vm.FormSchema)[1].Data[0].value = vm.TagsSelectedArray;
                return;
            } else {

                return
            }

        }
    }
}
</script>

<style scoped>
.scroldv {
    max-height: 490px;
    overflow-y: scroll;
}

.sel {
    background-color: #41b883;
    /* Add your selected item style */
    color: #fff;
}

.timeline-panel li {
       max-width: 95%;
    min-width: 95%;
    margin-bottom: 0;
}
.custom-checkbox .custom-control-label {
    margin-top: 5px;
    margin-left: 0px;
    padding-top: 3px;
    padding-left: 5px;
}
</style>
